@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap');

:root{
  --Blue: hsl(246, 80%, 60%);
  --Light-orange : hsl(15, 100%, 70%);/*(work)*/
  --Soft-blue : hsl(195, 74%, 62%);/*(play)*/
  --Light-red : hsl(348, 100%, 68%);/*(study)*/
  --Lime-green : hsl(145, 58%, 55%);/*(exercise)*/
  --Violet : hsl(264, 64%, 52%);/*(social)*/
  --Soft-orange : hsl(43, 84%, 65%);/*(self care)*/

  /*### Neutral*/

  --Very-dark-blue: hsl(226, 43%, 10%);
  --Dark-blue: hsl(235, 46%, 20%);
  --Desaturated-blue: hsl(235, 45%, 61%);
  --Pale-Blue: hsl(236, 100%, 87%);
}

body{
  margin: 0;
  font-family: 'Rubik', sans-serif;
  background-color: var(--Very-dark-blue);
}

main{
  margin: 10% auto;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(4,200px);
  gap: 1.5rem;
  justify-content: center;
}

.profile-card{
  display: flex;
  flex-direction: column;
  width: 200px;
  background-color: var(--Dark-blue);
  border-radius: 15px;
  grid-row: 1/3;
}
.profile-box{
  display: flex;
  flex-direction: column;
  background-color: var(--Blue);
  padding: 15px 15px 27px 15px ;
  border-radius: 15px;
}
.profile-box .profile_img{
  margin: 5px;
  padding: 4px 0;
  display: flex;
  align-items: center;
}
.profile-box img{
  width: 60px;
  border-radius: 50%;
  border: 2px solid white
}
.profile-info {
  margin: 5px;
  padding: 10px 0;
}
.profile-info span{
  color: var(--Pale-Blue);
  font-size: small;
}
.profile-box .profile-info h2{
  text-transform: capitalize;
  font-weight: 200;
  margin: 0;
  color: white;
  font-size: xx-large;
  margin: 3px 0;
}
.profile-card .timely-btns{
  display: flex;
  flex-direction: column;
  row-gap: 13px;
  margin: 15px 0;
  border-radius: 15px;
  padding: 15px;
}

.timely-btns span{
  margin: 0;
  width: auto;
  color: var(--Desaturated-blue);
  cursor: pointer;
}
.timely-btns span.active{
  color: white;
}
.dashboard-card{
  width: 200px;
  height: auto;
  border-radius: 15px;
}
.dashboard-card .card-icon{
  position: relative;
  z-index: 100;
  padding:0;
  margin: 0;
  border-radius: 15px;
  display: flex;
  justify-content: flex-end;
  float: right;
}
.dashboard-card .card-icon img{
  width: 50px;
  margin: 0;
  position: absolute;
}
.dashboard-card .card-info{
  position: relative;
  top: 35px;
  z-index: 100;
  border-radius: 15px;
  background-color: var(--Dark-blue);
  padding: 20px;
}
.dashboard-card .card-info:hover{
  background-color: var(--Desaturated-blue);
}
.card-title{
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: capitalize;
  font-weight: 400;
  color: white;
}
.card-title img{
  width: 15px;
}
.card-info .current-time{
  margin: 15px 0 0 0 ;
  color: #fff;
}
.card-info .current-time h2{
  font-size: 40px;
  font-weight: 300;
  margin: 0;
}
.card-info .prev-time{
  display: flex;
  color: var(--Pale-Blue);
  margin-bottom: -10px;
}
.card-info .prev-time p{
  font-size: small;
  font-weight: 400;
  text-transform: capitalize;
}

@media (max-width: 500px) {
  
  main{
    width: 90%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 10px;
  }

  .profile-card {
    width: auto;
  }
  .profile-box{
    display: flex;
    flex-direction: row;
    padding: 20px 15px ;
  }
  .profile-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 10px;
  }
  .profile-info .full-name{
    display: flex;
    column-gap: 10px;
  }
  .profile-box .profile-info h2{
    font-weight: 100;
    font-size: x-large;
  }
  .profile-card .timely-btns{
    display: flex;
    flex-direction: row;
    row-gap: none;
    text-align: center;
    padding: 10px;
  }
  .dashboard-card{
    width: 100%;
    height: 150px;
  }
  .card-info .card-timings{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 5px 0;
  }
  .card-info .current-time{
    margin: 0;
  }
  .card-info .prev-time p{
    font-size:medium;
    font-weight: 400;
    text-transform: capitalize;
  }
  .card-info .card-title{
    font-weight: 500;
  }
  .timely-btns {
    justify-content: space-around;
  }
}